.digital {
  display: flex;
  flex-wrap: wrap;
  position: relative;
  background-color: #fff;
  margin-bottom: 100px;
  &:before {
    content: '';
    width: 203px;
    height: 252px;
    background-image: url('../../static/img/digital-transform/mask.png');
    position: absolute;
    left: -95px;
    top: 20px;
  }
  &:after {
    content: '';
    width: 269px;
    height: 223px;
    background-image: url('../../static/img/digital-transform/mask.png');
    position: absolute;
    right: -100px;
    bottom: 125px;
    z-index: 4;
  }
  &_info {
    padding: 40px 60px;
    position: relative;
    width: 50%;
    box-sizing: border-box;
    &-title {
      font-size: 58px;
      text-transform: uppercase;
      letter-spacing: 4px;
      line-height: 67px;
      margin-bottom: 20px;
      color: #012950;
      &::before {
        content: '';
        width: 36px;
        height: 5px;
        background-color: currentColor;
        position: absolute;
        top: 70px;
        left: 0;
      }
    }
    &-subtitle {
      font-size: 30px;
      font-family: "akrobat-light";
      font-weight: normal;
    }
  }
  &_aside {
    width: 50%;
    &-blue {
      height: 125px;
      background-color: #A3D4F2;
      padding: 30px;
      box-sizing: border-box;
      margin-bottom: 20px;
      z-index: 2;
      position: relative;
      &::before {
        content: '';
        background-color: #B4C2C6;
        height: 1038px;
        width: 1px;
        position: absolute;
        top: 0;
        left: 0;
      }
      &::after {
        content: '';
        background-color: #B4C2C6;
        height: 1007px;
        width: 1px;
        position: absolute;
        top: 0;
        right: 0;
      }
    }
    &-image {
      height: 60px;
      object-fit: contain;
    }
    &-gray {
      height: 166px;
      background-color: #EAEAEA;
      position: relative;
      left: 100px;
      margin-bottom: 20px;
      z-index: 1;
      &::before {
        content: '';
        background-color: #B4C2C6;
        height: 460px;
        width: 1px;
        position: absolute;
        bottom: 166px;
        left: 0;
      }
    }
  }
  &_footer {
    background-color: #012950;
    padding: 20px 60px;
    max-width: 976px;
    width: 100%;
    box-sizing: border-box;
    position: relative;
    z-index: 3;
    &-list {
      display: flex;
      flex-direction: column;
      flex-wrap: wrap;
      max-width: 50%;
      max-height: 185px;
    }
    &-item {
      line-height: 22px;
      margin-bottom: 12px;
      margin-right: 40px;
      max-width: 235px;
      border-bottom: 1px solid #fff;
      color: #fff;
    }
    &-aside {
      display: flex;
      flex-direction: column;
      align-items: center;
      position: absolute;
      bottom: 32px;
      right: 60px;
    }
    &-image {
      position: absolute;
      bottom: 0;
    }
    &-link {
      letter-spacing: 2px;
      color: #fff;
      text-transform: uppercase;
      display: flex;
      align-items: center;
      text-decoration: none;
    }
    &-arrow {
      width: 85px;
      height: 1px;
      background-color: white;
      position: relative;
      left: 10px;
      display: block;
      &::before {
        content: '';
        width: 8px;
        height: 1px;
        background-color: white;
        bottom:3px;
        right: 0;
        position: absolute;
        transform: rotate(45deg)
      }
      &::after {
        content: '';
        width: 8px;
        height: 1px;
        background-color: white;
        top: 3px;
        right: 0;
        position: absolute;
        transform: rotate(-45deg)
      }
    }
  }
}